@import '../../styles/colors';

.Study{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background: $mainColor;

    & > div:nth-child(1){
        display: flex;
        flex-direction: column;
        width: 70%;
        height: 150px;

        & > div{
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            & > div{
                & > input{
                    margin-right: 10px;
                }
            }
        }

    }

    & > div:nth-child(2){
        display: flex;
        flex-direction: column;
        width: 70%;
        margin-top: 50px;

        & > div{
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-bottom: 20px;
        }
    }
}