@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body {
  margin: 0;
  padding: 0;
  font-family: 'NanumSquare', sans-serif;
  color: #fff; }

.App {
  width: 100%;
  height: 100%; }

.Color {
  background: #FF6F61; }

.nonColor {
  background-color: rgba(255, 111, 97, 0.7); }

.Header {
  display: flex;
  flex-direction: row;
  padding: 0px 20px;
  height: 60px;
  justify-content: space-between;
  align-items: center; }
  .Header a {
    color: #fff;
    text-decoration: none; }
  .Header > ul {
    list-style: none;
    text-decoration: none; }
    .Header > ul > a {
      display: list-item;
      float: left;
      margin-left: 20px; }

.Main {
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh - 60px);
  background-color: rgba(255, 111, 97, 0.7);
  background-size: 100%;
  flex-direction: column; }
  .Main > span {
    font-weight: 500;
    font-size: 42pt; }

.back {
  margin-top: -60px;
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%; }

.Voca {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  background: #FF6F61; }
  .Voca > ul {
    list-style: none;
    width: 70%; }
    .Voca > ul > li {
      border-bottom: 1px solid #fff;
      margin-bottom: 30px; }
      .Voca > ul > li > span {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between; }
        .Voca > ul > li > span > h3 {
          width: 100px; }
          .Voca > ul > li > span > h3 > a {
            text-decoration: none;
            color: #fff; }
  .Voca > div {
    display: flex;
    flex-direction: row;
    flex-flow: row wrap; }
    .Voca > div > div {
      display: flex;
      flex-direction: column;
      color: #000;
      width: 200px;
      height: 200px;
      background: #fff;
      margin: 30px;
      border-radius: 10px;
      padding: 10px; }
      .Voca > div > div > div {
        display: flex;
        justify-content: center; }
        .Voca > div > div > div > a {
          text-decoration: none;
          color: #000;
          text-align: center;
          border: 5px solid #000;
          border-radius: 5px;
          width: 100px; }

.Study {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background: #FF6F61; }
  .Study > div:nth-child(1) {
    display: flex;
    flex-direction: column;
    width: 70%;
    height: 150px; }
    .Study > div:nth-child(1) > div {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between; }
      .Study > div:nth-child(1) > div > div > input {
        margin-right: 10px; }
  .Study > div:nth-child(2) {
    display: flex;
    flex-direction: column;
    width: 70%;
    margin-top: 50px; }
    .Study > div:nth-child(2) > div {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 20px; }


